.subscribe {
	height: rem(105);

	background: url(../img/subs.jpg) no-repeat top center;
	background-size: cover;

	margin-bottom: rem(48);

	@include min768 {
		display: none;
	}

	&__wrap {
		display: flex;
		align-items: center;
		justify-content: center;
		height: 100%;

		p {
			font-size: rem(24);
			color: var(--c-grow);
			margin-right: rem(44);
		}


	}
}