.popup {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.25);

	// display: flex;
	align-items: center;
	justify-content: center;

	z-index: 100;

	display: none;

	&__video {
		margin: rem(20);
	}

	&__close {
		position: absolute;
		top: rem(10);
		right: rem(10);

		img {
			width: rem(24);
			height: rem(24);
		}
	}

	&__wrap {
		position: relative;
		width: rem(720);
		background-color: white;
		border-radius: rem(10);
		padding: rem(20);

		@include min768 {
			width: 100%;
			margin-left: 20px;
			margin-right: 20px;
		}

		h3 {
			text-align: center;
			margin-bottom: rem(40);

			@include min768 {
				max-width: rem(190);
				font-size: rem(24);
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 0.75rem) / 2);
			display: flex;
			// flex-wrap: wrap;
			margin-bottom: rem(20);

			padding: rem(13) rem(18);

			border-radius: rem(14);

			margin-right: rem(10);

			background: linear-gradient(180deg, #F3F3F3 0%, #E7DBD3 100%);

			@include min540 {
				padding: rem(7) rem(10);
			}


			&:nth-child(2n) {
				margin-right: 0;
			}

			@include min768 {
				width: 100%;
				margin-right: 0;
			}

			&__image {
				width: rem(34);
				height: rem(34);
				border-radius: 50%;
				background-color: var(--c-light);

				margin-right: rem(16);
				margin-top: rem(3);

				display: flex;
				align-items: center;
				justify-content: center;

				@include min540 {
					margin-right: rem(6);
				}

				img {
					width: rem(24);
					height: rem(24);
				}
			}

			&__text {
				b,p {
					// font-size: rem(18);

					@include min768 {
						font-size: rem(16);
					}

					@include min540 {
						font-size: rem(14);
					}
				}
			}
		}
	}
}

.popup.active {display: flex;}