.header {
	height: rem(127);
	background-color: white;

	@include min768 {
		height: rem(68);
	}

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		height: 100%;
	}

	&__nav {
		margin-top: rem(16);

		@include min1050 {
			display: none;
		}

		ul {
			
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			li {
				margin-right: rem(40);

				&:last-child {
					margin-right: 0;
				}

				a {
					transition: all 200ms ease;
					font-family: var(--f-helvetica);
					font-weight: 300;

					&:hover {
						color: var(--c-red-light);
					}
				}
			}
		}
	}

	&__logo {
		margin-right: rem(36);
		margin-left: rem(36);

		flex: 0 0 rem(186);
		max-width: rem(186);

		@include min768 {
			flex: 0 0 rem(106);
			max-width: rem(106);
			margin-left: 42%;
			margin-right: auto;

			// padding-left: rem(50);
		}

		@include min540 {
			margin-left: 38%;
		}
	}
	&__nav {
		flex: 0 0 auto;
		margin-left: rem(32);
	}
	&__right {
		flex: 1 1 auto;
		max-width: rem(480);

		@include min768 {
			max-width: 100%;
			flex: 0 0 auto;
		}
	}

	&__right {
		margin-top: rem(16);

		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		@include min768 {
			justify-content: flex-end;
		}

		span, button {
			font-family: var(--f-helvetica);
			font-weight: 300;
		}

		button {
			transition: all 200ms ease;

			@include min768 {
				display: none;
			}

			&:hover {
				color: var(--c-red-light);
			}
		}
	}
}