:root {
	--f-default: 'Montserrat';
	--f-liberation: 'Liberation Serif';
	--f-helvetica: 'HelveticaNeueCyr';
	--c-text: #474747;
	--c-gray: #C4C4C4;
	--c-grow: #746057;
	--c-red: #DA2928;
	--c-red-light: rgba(218, 41, 40, 0.9);
	--c-white: #ffffff;
	--c-light: #A79085;
}

* {
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
	margin: 0;
	padding: 0;
	font-size: rem(18);
	color: var(--c-text);
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

h1, h2, h3 {
	font-family: var(--f-liberation);
	font-weight: normal;
}

h1 {
	font-size: rem(56);

	@include min768 {
		font-size: rem(26);
	}

	@include min540 {
		font-size: rem(18);
	}
}

h2, h3 {
	font-size: rem(36);

	@include min768 {
		font-size: rem(26);
	}

	@include min540 {
		font-size: rem(18);
	}
}

p {
	font-size: rem(18);
}

img {
	// opacity: 0.15;
}

body {
	position: relative;
	font-size: rem(18);
	font-family: var(--f-default);
	// height: 100%;
	color: var(--c-text);
	overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.container {
	max-width: 1230px;
	margin: 0 auto;

	@include min1250 {
		max-width: 100%;
		margin-left: 20px;
		margin-right: 20px;
	}
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.button_border {
	padding: rem(13) rem(32);
	border-radius: rem(40);
	border: rem(2) solid var(--c-grow);
	font-weight: 300;

	display: flex;
	align-items: center;
	justify-content: center;

	color: var(--c-grow);


	transition: all 200ms ease;

	&:hover {
		background-color: var(--c-grow);
		color: var(--c-white);
	}
}