.header_info {
	// height: rem(906);

	margin-bottom: rem(48);

	@include min540 {
		margin-bottom: rem(24);
	}

	.container {
		display: flex;
		flex-direction: column;
	}
	&__top {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: rem(10);
		height: rem(496);

		@include min768 {
			height: rem(300);
		}

		@include min540 {
			height: rem(169);
		}

		.header_info__first {
			width: rem(936);

			// background-color: rgba(0,0,0,.3); //b

			background: url(../img/header_1.jpg) no-repeat top center;
			background-size: cover;

			display: flex;
			flex-direction: column;
			justify-content: center;
			height: 100%;

			padding-left: rem(57);

			@include min1250 {
				width: 73%;
			}

			@include min768 {
				width: 100%;
				padding-left: rem(20);
			}

			@include min540 {
				background: url(../img/header_1_x3.jpg) no-repeat top center;
				background-size: cover;
			}

			h1 {
				color: var(--c-red);
				margin-bottom: rem(14);

				@include min540 {
					margin-bottom: rem(5);
				}
			}

			p {
				max-width: rem(410);
				font-size: rem(24);
				font-weight: 300;
				color: var(--c-red);
				margin-bottom: rem(4);

				@include min768 {
					font-size: rem(18);
					max-width: rem(100);
				}

				@include min540 {
					font-size: rem(12);
					
				}
			}

			.price {
				font-size: rem(56);
				font-weight: 300;
				color: var(--c-light);

				@include min768 {
					font-size: rem(18);
				}

				@include min540 {
					font-size: rem(14);
				}
			}
		}

		.header_info__last {
			width: rem(287);
			height: 100%;

			display: flex;
			justify-content: center;
			align-items: center;

			// background-color: rgba(0, 0, 0, .3); //b
			background: url(../img/header_2.jpg) no-repeat top center;
			background-size: cover;

			@include min1250 {
				width: 26%;
			}

			@include min768 {
				display: none;
			}

			.play {
				width: rem(100);
				height: rem(100);
				border-radius: 50%;
				background-color: rgba(196, 196, 196, 0.6);

				display: flex;
				align-items: center;
				justify-content: center;

				img {
					pointer-events: none;
					width: rem(56);
					height: rem(56);
					margin-left: rem(12);
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		height: rem(399);

		@include min768 {
			height: rem(254);
		}

		.header_info__first {
			width: rem(805);

			background-color: rgba(0, 0, 0, .3); //b

			@include min1250 {
				width: 63%;
			}

			@include min768 {
				width: 100%;
				height: rem(169);
				margin-bottom: rem(10);
			}

			.swiper-container {
				height: 100%;
				

				.swiper-slide {
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: flex-end;

					overflow: hidden;

					h2 {
						margin-bottom: rem(4);
						color: var(--c-white);
					}

					p {
						font-weight: 300;
						color: var(--c-white);
						margin-bottom: rem(46);

						@include min540 {
							font-size: rem(12);
						}
					}

					img {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						object-fit: cover;
						z-index: -1;
					}
				}

				.swiper-pagination-bullets {
					bottom: rem(20);
				}

				.swiper-pagination-bullet {
					width: rem(40);
					height: rem(5);
					border-radius: rem(4);
					background-color: var(--c-gray);
					opacity: 1;
					margin: 0 rem(11) 0 0;

					@include min540 {
						width: rem(16);
						height: rem(3);
						margin: 0 rem(4) 0 0;
					}

					&:last-child {
						margin: 0;
					}
				}

				.swiper-pagination-bullet-active {
					background-color: var(--c-white);
				}
			}
		}
		.header_info__last {
			width: rem(418);

			// background-color: rgba(0, 0, 0, .3); //b

			background: url(../img/header_3.jpg) no-repeat top center;
			background-size: cover;

			display: flex;
			align-items: center;
			justify-content: center;

			@include min1250 {
				width: 36%;
			}

			@include min768 {
				width: 100%;
				height: rem(76);
			}

			@include min540 {
				background: url(../img/header_3_x3.jpg) no-repeat top center;
				background-size: cover;
			}

			span {
				font-size: rem(56);
				font-weight: 300;
				color: var(--c-white);
				max-width: rem(216);
				display: block;
				text-align: center;

				@include min768 {
					font-size: rem(26);
				}

				@include min540 {
					font-size: rem(18);
				}
			}
		}
	}
}