.news {
	height: rem(392);
	background: linear-gradient(180deg, #F7F2EE 0%, #F3D8C7 100%);
	padding: rem(40) 0;

	margin-bottom: rem(60);

	display: grid;
	grid-template-columns: calc((100% - 1230px)/2) 3fr 7fr calc((100% - 1230px)/2);

	@include min1250 {
		display: flex;
		flex-wrap: wrap;
		padding-left: 20px;
		padding-right: 20px;
	}

	@include min {
		height: rem(300);
		padding: rem(20);

		background: url(../img/news_bg.jpg) no-repeat top center;
		background-size: cover;
	}

	@include min540 {
		justify-content: space-between;

		margin-bottom: rem(24);
	}

	.container {
		// display: flex;
		// // flex-wrap: wrap;
		// height: 100%;
		// display: grid;
		// grid-template-columns: calc((100% - 1230px)/2) 3fr 7fr calc((100% - 1230px)/2);
	}

	&__left {

		grid-column: 2 / span 1;

		height: 100%;


		display: flex;
		flex-direction: column;
		justify-content: center;
		// align-items: center;

		.smart {
			display: none;

			@include min768 {
				display: block;
			}
		}

		@include min1250 {
			flex: 0 0 23%;
		}

		@include min540 {
			flex: 0 0 58%;
		}

		p {
			font-size: rem(30);
			color: var(--c-grow);
			margin-bottom: rem(26);

			@include min1250 {
				font-size: rem(22);
			}

			@include min {
				font-size: rem(18);
			}

			@include min768 {
				font-size: rem(14);

				// display: none;

				b {
					font-weight: normal;
				}
			}
		}

		a {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			img {
				margin-right: rem(13);
			}

			font-size: rem(30);
			font-weight: bold;
			color: var(--c-grow);

			@include min1250 {
				font-size: rem(22);
			}

			@include min {
				font-size: rem(18);
			}

			@include min768 {
				display: none;
			}
		}

		@include min768 {
			.button_border {
				border-width: rem(2);
				font-size: rem(12);
				padding: rem(8) rem(26);
				align-self: flex-start;
			}
		}
	}

	&__right {
		grid-column-end: span 2;

		display: flex;

		align-items: center;

		padding-left: rem(80);
		// flex-wrap: wrap;

		@include min1250 {
			flex: 0 0 70%;
			max-width: 70%;
			padding-left: rem(30);
		}

		@include min {
			height: 100%;
		}

		@include min540 {
			flex-wrap: wrap;
			flex-direction: column;
			max-width: 30%;
			flex: 0 0 30%;
			padding-left: 0;
			margin-right: rem(25);
			margin-top: rem(-25);
			height: auto;
		}

		img {
			
			object-fit: cover;
			&:first-child, &:nth-child(2) {
				width: rem(312);
				height: rem(312);
				margin-right: rem(25);

				@include min {
					height: 100%;
					margin-right: rem(15);
				}

				@include min540 {
					width: rem(100);
					height: rem(100);
					margin-right: 0;
					margin-bottom: rem(7);

					&:last-child {
						margin-bottom: 0;
					}
				}
			}

			&:last-child {
				width: rem(245);
				height: rem(312);
				
				@include min {
					height: 100%;
					// margin-right: rem(15);
				}

				@include min540 {
					width: rem(100);
					height: rem(100);
				}
			}
		}
	}
}