.product {
	margin-bottom: rem(60);

	@include min540 {
		margin-bottom: rem(30);
	}

	h2 {
		text-align: center;
		margin-bottom: rem(40);

		@include min540 {
			margin-bottom: rem(24);
		}
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 4rem) / 6);
			margin-right: rem(12);

			@include min1050 {
				width: calc((100% - 1.5rem) / 3);
				margin-bottom: rem(20);

				&:nth-child(3n) {
					margin-right: 0;
				}
			}

			@include min540 {
				width: calc((100% - 0.75rem) / 2);
				// margin-right: 0;

				&:nth-child(2n) {
					margin-right: 0;
				}

				&:nth-child(3n) {
					margin-right: rem(12);
				}
			}

			&:last-child {
				margin-right: 0;
				
			}

			&__image {
				position: relative;
				height: rem(362);
				margin-bottom: rem(18);
				overflow: hidden;

				@include min540 {
					height: rem(189);
					margin-bottom: rem(10);
				
				}

				.count {
					position: absolute;
					bottom: rem(9);
					right: rem(13);

					width: rem(60);
					height: rem(60);
					border-radius: 50%;
					background-color: var(--c-red-light);

					display: flex;
					align-items: center;
					justify-content: center;

					font-weight: bold;
					font-family: var(--f-helvetica);
					color: var(--c-white);
				}

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;

					@include min768 {
						height: rem(189);
						object-position: top;
					}
				}
			}

			&__text {
				display: flex;
				flex-direction: column;

				p {
					// color: var(--c-gray);
					font-family: var(--f-helvetica);
					font-weight: 300;
					margin-bottom: rem(14);

					@include min540 {
						font-size: rem(9);
						margin-bottom: rem(6);
					}
				}

				b {
					// color: var(--c-gray);
					font-family: var(--f-helvetica);
					font-weight: bold;
					margin-bottom: rem(14);

					@include min540 {
						font-size: rem(14);
						margin-bottom: rem(11);
					}
				}

				b.text_gray {
					position: relative;
					align-self: flex-start;
					color: var(--c-gray);

					&::before {
						content: "";
						position: absolute;
						top: 50%;
						left: 0;
						width: 100%;
						height: rem(1);
						background-color: var(--c-gray);
					}
				}

				.button_border {
					padding: rem(8);
					font-size: rem(14);
					font-family: var(--f-helvetica);
					font-weight: 300;

					@include min540 {
						font-size: rem(9);
						align-self: flex-start;
					}
				}
			}

		}
	}
}