.contact {
	width: 100%;
	
	h3 {
		text-align: center;
		margin-bottom: rem(30);

		@include min660 {
			display: none;
		}
	}

	&__wrap {
		height: rem(537);
		background: url(../img/contact_bg.jpg) no-repeat top center;
		background-size: cover;

		display: grid;
		grid-template-columns: calc((100% - 1440px)/2) 4fr 6fr calc((100% - 1440px)/2);

		@include min660 {
			display: flex;
			height: auto;
			background: none;
		}

	}

	&__left {
		grid-column: 1 / span 2;

		overflow: hidden;

		@include min660 {
			display: none;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__right {
		padding-left: rem(55);

		grid-column-end: span 1;

		display: flex;
		flex-direction: column;
		justify-content: center;

		@include min540 {
			padding: 0 rem(42);
		}

		@include min350 {
			padding: 0 rem(20);
		}

		> p {
			font-size: rem(24);
			color: var(--c-grow);
			margin-bottom: rem(50);

			@include min660 {
				font-size: rem(18);
				margin-bottom: rem(20);
			}
		}
		
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		max-width: rem(753);
		width: 100%;

		@include min1050 {
			max-width: 100%;
		}

		.item {
			display: flex;
			flex-wrap: wrap;

			width: 50%;

			margin-bottom: rem(30);

			@include min1050 {
				width: 100%;
			}

			&__image {
				margin-right: rem(16);
				display: flex;
				align-items: center;
				justify-content: center;

				width: rem(50);
				height: rem(50);
				border-radius: 50%;
				background-color: var(--c-light);

				@include min660 {
					width: rem(34);
					height: rem(34);

				}

				img {
					width: rem(36);
					height: rem(36);
					background-size: 100%;

					@include min660 {
						width: rem(24);
						height: rem(24);
					}
				}
			}

			&__text {
				b, p {
					color: var(--c-grow);

					@include min350 {
						font-size: rem(14) !important;
					}
				}

				b {
					font-size: rem(24);

					@include min660 {
						font-size: rem(18);
					}
				}

				p {
					// font-size: rem(18);
				}
			}
		}
	}
}